:root{
  --primary-color: #161616;
  --secondary-color: #1b1c1d;

  --primary-text-color: #ffffff;
  --secondary-text-color: #808080;

  
  --accent-color: rgb(66, 66, 66);
  --accent-color-dark: #1b1c1d;
  --accent-color-light: #808080;
  --effect-color: #cecece;

}


html, body, #root, #app, #app>div {
  height: auto;
  min-height: 100%;
}

.appContainer{
  background: var(--primary-color);
  margin: 0px;
  height: inherit;
  margin-right: auto;
  margin-left: auto;
}

.gridContainer {
  display: grid;
  grid-template-areas:
    "header"
    "content"
    "comments"
    "footer";
  grid-gap: 30px;
}

.contentContainer{
  background: var(--secondary-color);
  grid-area: content;
  width: -webkit-fill-available;
  width: -moz-available;
  max-width: 1200px;
  display: flex;
  justify-self: center;
  border-radius: 5px;
}

.contentGrid{
  background: var(--secondary-color);
  display: grid;
  grid-template-areas:
    "header header header"
    "box1 box2 box2"
    "box3 box4 box4";
  grid-gap: 30px;
  width: -webkit-fill-available;
  width: -moz-available;
  border-radius: 5px;
}

.contentHeaderBox{
  margin-top:30px;
  background-image: url("../images/code_bg_2.png");
  border-radius: 5px;
  grid-area: header;
}
.contentHeaderBox::after{
  background-color: rgba(0,0,0,0.5);
}
.contentProfileBox{
  grid-area: box1;
}
.contentEducationBox{
  grid-area: box2;
}
.contentExperienceBox{

  grid-area: box4;

}
.contentSkillsBox{
  grid-area: box3;
}

.contentBox{
  margin: 10px;
  padding: 10px;

  -webkit-box-shadow: 0 0 10px var(--accent-color) cubic-bezier(0.165, 0.84, 0.44, 1);;
  box-shadow: 0 0 10px var(--accent-color);
  border-radius: 5px;
  width: -webkit-fill-available;
  width: -moz-available;
  transition: box-shadow -webkit-box-shadow 0s;
}

.contentBox:hover{

  -webkit-box-shadow: 0 0 10px var(--accent-color-light);
  box-shadow: 0 0 15px var(--accent-color-light);
  transition: 0.5s;
  transform: scale(1.02);

}

.headerMenuContainer{
  background: var(--secondary-color);
  max-height: 65px;
  grid-area: header;
  width: -webkit-fill-available;
  width: -moz-available;
  max-width: 1200px;
  display: flex;
  justify-self: center;
  border-radius: 5px;
}

.gridHeaderMenu {
  background: var(--secondary-color);
  display: grid;
  grid-template-areas:
    "left center right";

  grid-gap: 30px;
  width: -webkit-fill-available;
  width: -moz-available;
  border-radius: 5px;
}
.headerMenuLogo{
  display: flex;
  grid-area: left;
  justify-content: flex-start;
  text-align: right;
  padding:10px;
}
.headerMenuList{
  display: flex;
  grid-area: right;
  justify-content: flex-end;
  text-align: right;
  padding:10px;
  width: -webkit-fill-available;
  width: -moz-available;
}


.footerContainer{
  background: var(--secondary-color);
  text-align: center;
  position: relative;
  width: -webkit-fill-available;
  width: -moz-available;
  bottom: 0;

}

.commentSectionContainer{
  background: var(--secondary-color);
  grid-area: comments;
  max-width: 1200px;
  width: -webkit-fill-available;
  width: -moz-available;
  justify-self: center;
  border-radius: 5px;
}

.commentSectionList{
  padding: 0px 10px;
  margin: 0px;
}

.commentListItem{
  background: var(--secodary-color);
  border-radius: 5px;
  margin: 10px 0px;
  list-style: none;
  padding: 15px 15px;
  width: max-content;
  box-shadow: 0 0 5px var(--accent-color);
}

.divider{
  width: -webkit-fill-available;
  width: -moz-available;
  background: var(--accent-color);
  height: 2px;
  border-radius: 2px;
}

.userNameForm{
  background: var(--secondary-color);
  margin: 5px;
  padding: 5px;
  border: none;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  width: -webkit-fill-available;
  width: -moz-available;
  border-bottom: 2px solid var(--accent-color-light);
  resize: none;
}
.userNameForm:focus{
  outline: none;
  border-bottom-color: var(--accent-color-light) radial-gradient(circle, transparent 1%, var(--accent-color-light) 1%) center/15000%;
}

.commentContainer{
  margin: none;
  padding: 10px !important;
}

.commentForm{
  background: var(--secondary-color);
  margin: 5px;
  padding: 5px;
  width: -webkit-fill-available;
  width: -moz-available;
  border: none;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  border-bottom: 2px solid var(--accent-color-light);
  resize: none;
  transition: border-bottom-color 0.5s;
}
.commentForm:focus{
  outline: none;
  border-bottom-color: var(--accent-color-light) radial-gradient(circle, transparent 1%, var(--accent-color-light) 1%) center/15000%;
}

.commentButtonsContainer{
  float: right;

  padding: 0px;
  width: -webkit-fill-available;
  width: -moz-available;
}


.notificationContainerInner{
  width: max-content;
  height: max-content;
  float: right;
  min-width: 300px;
  background: var(--secondary-color);
  position: fixed;
  right: 30px;
  bottom: 60px;
  -webkit-box-shadow: 0 0 5px var(--accent-color);
  box-shadow: 0 0 5px var(--accent-color);
  
  border-radius: 5px;
  transition: box-shadow 0s cubic-bezier(0.165, 0.84, 0.44, 1)

}
.notificationContainerInner:hover{
  box-shadow: 0 0 10px var(--accent-color-light);
  border-radius: 5px;
  transition-duration: 0.5s

}


.notificationGrid{
  display: grid;
  padding: 0px 30px;
  grid-template-areas:
    "header"
    "message"
    "buttons"
    "timer";
  grid-gap: 5px;

}

.notificationTimer{
  height: 10px;
  display: flex;
  
}

.timeRemaining{
  background: linear-gradient(90deg, rgba(0,0,0,0) 0%, rgba(255,255,255,1) 100%);
  height: 10px;
  width: 100%;
  animation-name: timer;
  animation-timing-function: linear;
  
}
@keyframes timer {
  from {width: 100%;}
  to {width: 0%;}
}

.contentList{
  list-style:none;
  margin: 5px;
  padding: 5px;
}

.addressLine{
  text-align: center;

}

.logo{
  padding-top: 30px;
  max-width: 200px;
  max-height: 120px;
}

.loginContainer{
  
  background: var(--secondary-color);
  width: -webkit-fill-available;
  width: -moz-available;
  height: max-content;
  max-width: 400px;
  max-height: 300px;
  border-radius: 5px;
  margin:auto;
  padding: 20px;

  box-shadow: 0 0 5px var(--accent-color);
  transition: box-shadow 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.loginContainer:focus-within{
  box-shadow: 0 0 10px var(--accent-color-light)
}

.inputContainer{
  background: var(--secondary-color);
  margin: 20px 0px;
  padding: 5px;
  width: -webkit-fill-available;
  width: -moz-available;
  border: none;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  resize: none;
}

.inputFocusLine{
  position: relative;
  bottom: 2px;
  margin: auto;
  width: 0%;
  border-bottom: 2px solid white;
  transition: linear width 0s;
}
.inputFocusLineComment{
  bottom: 2px;
}

.input{
  vertical-align: top;
  background: var(--secondary-color);
  width: -webkit-fill-available;
  width: -moz-available;
  border: none;
  margin: 0px;
  overflow: hidden;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  resize: none;
  padding: 0px;
  border-bottom: 2px solid var(--accent-color-light);
}

.input:focus{
  outline: none;
}

.input:focus + .inputFocusLine{
  border-bottom: 2px solid white;
  width:100%;
  transition-duration: 0.3s;
}

.loginForm{

  background: var(--secondary-color);
  margin: 20px 5px;
  padding: 5px;
  width: -webkit-fill-available;
  width: -moz-available;
  border: none;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  border-bottom: 2px linear-gradient(to right, var(--accent-color) 50%, white 50%);
  resize: none;
  transition: border-bottom-color 2s;
}

.loginForm:focus{
  outline: none;
}
.centerAlign{
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.menuPadding{
  opacity: 0;

}