
.educationContainerAlt{
  background: var(--secondary-color);
  z-index: 1;
  grid-area: section3;
  width: 100%;
  height: 500px;
}
.educationContainerInner{
  z-index: 1;
  display: flex;
  max-width: 1200px;
  height: 85%;
  margin: auto;
}
.educationListItem{
  z-index: 2;
  background: rgba(22, 22, 22, 0.85);
  float: left;
  list-style-type: none;

  margin:auto;
  padding: 30px;
  -webkit-box-shadow: 0 0 10px var(--accent-color) cubic-bezier(0.165, 0.84, 0.44, 1);;
  box-shadow: 0 0 10px var(--accent-color);
  border-radius: 5px;
  transition: box-shadow -webkit-box-shadow 0s;
}
.educationListItem:hover{
  -webkit-box-shadow: 0 0 10px var(--accent-color-light);
  box-shadow: 0 0 15px var(--accent-color-light);
  transition: 0.5s;
  transform: scale(1.2);
}

.educationTextContent{
  z-index: 2;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-size: 1.5rem;
}

.educationTitle{
  margin:auto;
  display: flex;
  z-index: 2;
  height: 15%;
  width: auto;
}
.educationTitleText{
  z-index: 2;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-size: 3em;
  text-align: center;
  font-weight: 700;
  padding:10px;
  margin: auto;
  opacity: 1;
}

.imgBg1{
  background: url("../images/Lyseonpuiston_lukio.jpeg");
  background-position: center; /* Center the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  width: 100%;
  height: 500px;
  position: absolute;
  z-index: 0;
  visibility: hidden;

}
.imgBg2{
  background: url("../images/oulu_university2.png");
  background-position: left center; /* Center the image */
  background-size: cover; /* Resize the background image to cover the entire container */
  width: 100%;
  height: 500px;
  position: absolute;
  z-index: 0;
  visibility: hidden;
}