.skillsContainer{
  background: var(--secondary-color);
  grid-area: section5;
  width: 100%;
  height: 500px;
}

.skillsTitle{
  height: 15%;
  width: auto;
  
}
.skillsTitleText{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-size: 3em;
  text-align: center;
  font-weight: 700;
  padding:10px;
  margin: 0;
}

.skillsTextContent{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-size: 2rem;
}

.skillsContainerInner{
  display: flex;
  max-width: 1200px;
  height: 70%;
  margin: auto;
}
.skillsArrow{
  margin:auto;
  cursor: pointer;
  fill: var(--effect-color)
}
.skillsArrow:hover{
  fill: white;
  transform: scale(1.2);
  transition: 0.5s;
  
}




.skillsContent{
   width: 100%;
   margin: auto;
   text-align: center;
}

.tabsBubblesContainer{
  height: 15%;
  width: 100%;
  max-width: 1200px;
  margin: auto;
}
.tabsBubblesContainerInner{
  margin:auto;
  width: fit-content;
  width: -moz-fit-content;
}
.selectedIcon{
  cursor: pointer;
  fill: white;
  margin: 10px;
}
.selectedIcon:hover{
  transform: scale(1.5);
  transition: 0.3s;
}

.unselectedIcon{
  cursor: pointer;
  fill: gray;
  margin: 10px;
}
.unselectedIcon:hover{
  fill: white;
  transform: scale(1.5);
  transition: 0.3s;
}