.jobContainer{
  background: var(--primary-color);
  grid-area: section4;
  width: 100%;
  height: 500px;
}

.jobsTitle{
  height: 15%;
  width: auto;
  
}
.jobsContainerInner{
  display: flex;
  max-width: 1200px;
  height: 85%;
  margin: auto;
}
.jobsTitleText{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-size: 3em;
  text-align: center;
  font-weight: 700;
  padding:10px;
  margin: 0;
}

.jobsTextContent{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-size: 1.5rem;
}

.jobsListItem{
  background: var(--secondary-color);
  float: left;
  list-style-type: none;

  margin:auto;
  padding: 30px;
  -webkit-box-shadow: 0 0 10px var(--accent-color) cubic-bezier(0.165, 0.84, 0.44, 1);;
  box-shadow: 0 0 10px var(--accent-color);
  border-radius: 5px;
  transition: box-shadow -webkit-box-shadow 0s;
}
.jobsListItem:hover{
  -webkit-box-shadow: 0 0 10px var(--accent-color-light);
  box-shadow: 0 0 15px var(--accent-color-light);
  transition: 0.5s;
  transform: scale(1.2);
}