input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active  {
    color: blue !important;
}

.textPrimary{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-size: 1em;
  padding: 2px;
}

.footerText{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  padding: 10px 0px;
  margin: auto;
  font-size: 1em;
}

.headerText{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-weight: 700;
  text-align: center;
  font-size: 1em;
  padding: 10px;
}

.headerTextLarge{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-weight: 900;
  text-align: center;
  font-size: 1.2em;
  padding: 10px;
}

.headerTextLeft{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-weight: 700;
  text-align: left;
  font-size: 1em;
  padding: 10px;
}



.logoText{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-weight: 700;
  font-size: 1.2em;
  padding: 10px;
}



.commentText{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  padding-top: 5px;
}

.commentDate{
  color: var(--secondary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-weight: 400;
  text-align: left;
  font-size: 0.8em;
}
.commentAuthor{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-weight: 400;
  text-align: left;
  font-size: 0.8em;
}

.notificationHeader{
  grid-area: "header";
}
.notificationDescription{
  grid-area: "message";
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  text-align: center;
  font-size: 0.8em;
  padding: 10px 0px;

}

.textContent{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-size: 1.2em;
}

.bottomPadding30{
  padding-bottom: 30px;
}

.menuItemText{
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-size: 1.2em;
  display: block;
  font-weight: 500;
}


