.CSAltContainer{
  background: var(--primary-color);
  grid-area: section6;
  width: 100%;
}
.CSAltContainerInner{
  background: var(--primary-color);
  max-width: 1200px;
  width: -webkit-fill-available;
  width: -moz-available;
  justify-self: center;
  margin: auto;
}
.commentListItem{
  background: var(--primary-color);
  border-radius: 5px;
  margin: 20px 0px;
  list-style: none;
  padding: 15px 15px;
  width: max-content;
  box-shadow: 0 0 5px var(--accent-color);
}
.commentListItem:hover{
  -webkit-box-shadow: 0 0 10px var(--accent-color-light);
  box-shadow: 0 0 10px var(--accent-color-light);
  transition: 0.5s;
}

.divider{
  width: -webkit-fill-available;
  width: -moz-available;
  background: var(--accent-color);
  height: 2px;
  border-radius: 2px;
}

.commentContainer{
  margin: none;
  padding: 10px !important;
}

.commentForm{
  background: var(--primary-color);
  margin: 5px;
  padding: 5px;
  width: -webkit-fill-available;
  width: -moz-available;
  border: none;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  border-bottom: 2px solid var(--accent-color-light);
  resize: none;
  transition: border-bottom-color 0.5s;
}
.commentForm:focus{
  outline: none;
  border-bottom-color: var(--accent-color-light) radial-gradient(circle, transparent 1%, var(--accent-color-light) 1%) center/15000%;
}

.commentButtonsContainer{
  float: right;
  padding: 0px;
  width: -webkit-fill-available;
  width: -moz-available;
}

.inputContainerAlt{
  background: var(--primary-color);
  margin: 20px 0px;
  padding: 5px;
  width: -webkit-fill-available;
  width: -moz-available;
  border: none;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  resize: none;

}

.inputFocusLine{
  position: relative;
  bottom: 2px;
  margin: auto;
  width: 0%;
  border-bottom: 2px solid white;
  transition: linear width 0s;

}
.inputFocusLineComment{
  bottom: 2px;
}


.inputAlt{
  vertical-align: top;
  background: var(--primary-color);
  width: -webkit-fill-available;
  width: -moz-available;
  border: none;
  margin: 0px;
  overflow: hidden;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-weight: 400;
  text-align: left;
  font-size: 1em;
  resize: none;
  padding: 0px;
  border-bottom: 2px solid var(--accent-color-light);
}

.inputAlt:focus{
  outline: none;
}
.inputAlt:focus + .inputFocusLine{
  border-bottom: 2px solid white;
  width:100%;
  transition-duration: 0.3s;
}