.infoContainer{
  display: flex;
  background: url("../images/code_bg_75.png");
  grid-area: section1;
  width: 100%;
  height: 500px;
}
.infoContainerInner{
  max-width: 1200px;
  margin: auto;
}
.infoHeader{
  color:var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  text-align: center;
  font-size: 1em;
  padding: 20px;
  margin: 0px;
  transition: font-size 1s;
  font-size: 3em;
}
.contactInfoText{
  text-align: center;
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  padding: 2px;
}