.altMenuContainer{
  z-index: 3;
  width: 100%;
  height: fit-content;
  top: 0;
  position: fixed;
  background: var(--secondary-color);
  -webkit-box-shadow: 10px var(--accent-color-light);
  box-shadow: 0px 0px 10px var(--accent-color);
  transition: top 0.3s;
}

.altMenuGrid{
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-areas:
    "left center right";
  grid-gap: 30px;
  width: -webkit-fill-available;
  width: -moz-available;
  border-radius: 5px;
}

.altMenuLogo{
  grid-area: left;
  padding:10px;
}

.altMenuButtonList{
  color: var(--primary-text-color);
  list-style-type: none;
  padding: 0;
  margin: 0;
  grid-area: right;
}
