.profileContainer{
  background: linear-gradient(to bottom, rgb(255, 50, 0), rgb(255, 100, 0));
  grid-area: section2;
  width: 100%;
  height: 500px;
}
.profileContainerInner{
  max-width: 1200px;
  margin: auto;
  height: 85%;
  display: flex;
}

.profileContentContainer{
  display: flex;
  width: fit-content;
  
}
.icon{
  fill:white;
  padding: 5px;
  font-size: 1.5em;
}
.textContentProfile{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-size: 1.2em;
  align-self: center;
}
.contentList{
  list-style: none;
  margin: 0px;
  padding: 10px 0px;
}

.profileTextContent{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  margin: 0px;
  font-size: 1.5rem;
}

.profileTitle{
  height: 15%;
  width: auto;

  
}
.profileTitleText{
  color: var(--primary-text-color);
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  font-size: 3em;
  text-align: center;
  font-weight: 700;
  padding:10px;
  margin: 0;
}
.profileListContainer{
  width: fit-content;
  margin: auto;
}
